/**
 * Basic typography style for copy text
 */

// body {
// 	@include typi('base');
// }

h1,
h2,
h3,
h4,
h5,
h6,
dt,
th,
legend {
  font-weight: 300;
}

dt,
th {
  font-weight: 500;
}

h1,
.h1 {
  font-size: 2.5em;
}

h2,
.h2 {
  font-size: 2em;
}

h3,
.h3 {
  font-size: 1.75em;
}

h4,
.h4 {
  font-size: 1.5em;
}

h5,
.h5 {
  font-size: 1.25em;
}

h6,
.h6 {
  font-size: 1em;
}

.lead {
  font-size: 0.75em;
  font-weight: 300;
}

.display-1 {
  font-size: 5.5em;
  font-weight: 300;
  line-height: 1.2;
}

.display-2 {
  font-size: 5em;
  font-weight: 300;
  line-height: 1.2;
}

.display-3 {
  font-size: 4em;
  font-weight: 300;
  line-height: 1.2;
}

.display-4 {
  font-size: 3em;
  font-weight: 300;
  line-height: 1.2;
}

hr {
  margin-top: 1em;
  margin-bottom: 1em;
  border: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}
